@import url(https://fonts.googleapis.com/css2?family=Paytone+One&family=Raleway&display=swap);
.pokemon-card {
    color: #032B43;
    background-color: #fdfcf1;
    height: 310px;
    width: 200px;
    display: flex;
    justify-content: center;
    margin: 20px;
    border-radius: 10px;
    line-height: 1.5rem;
}

.pokemon-card .image {
    display: flex;
    justify-content: center;
}

.pokemon-card img {
    width: 96px;
    height: 96px;
}

.pokemon-card ul {
    list-style: none;
}

.pokemon-card ul.abilities {
    list-style-type: square;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1rem;
}

.pokemon-card ul ul li {
    /* display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; */
}

.pokemon-card ul li {
    display: flex;
    justify-content: center;
}

.pokemon-card h3 {
    font-family: 'Paytone One', sans-serif;
    text-transform: uppercase;
    font-size: 1.5rem;
    margin-top: 10px;
    font-weight: bold;
}

.pokemon-card h4 {
    margin-right: 0.5rem;
    display: inline;
    font-weight: bold;
}


.pokemon-card h4 svg {
    margin-bottom: -4px;
}
* {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Raleway', sans-serif;
    display: flex;
    justify-content: center;
    background-color: #032B43;
}

#root {
    max-width: 1220px;
}

header {
    display: flex;
    justify-content: center;
}

.pokemon-list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
}
.nav-buttons {
    display: flex;
    justify-content: center;
    margin: 20px;
}

.nav-buttons button {
    background-color: #fdfcf1;
    margin: 0 20px;
    border-style: none;
    border-radius: 4px;
    height: 40px;
    width: 100px;
    display: flex;
    justify-content: center;
    cursor: pointer;
}

.nav-buttons button.previous span {
    margin-left: -0.5rem;
}

.nav-buttons button.next span {
    margin-right: -0.5rem;
}

.nav-buttons button span {
    display: flex;
    align-items: center;
}

.nav-buttons button:disabled {
    opacity: 0.5;
}
